@import 'web/variables.scss';









































































































































body {
	background-color: $aoc-background;
	#app {
		.v-btn {
			text-transform: none;
		}
		div {
			.aoc-em {
				color: #ffffff;
				text-shadow: 0 0 5px #ffffff;
			}
		}
		a, button#run {
			text-decoration: none;
			color: #009900;
		}
		a:hover, a:focus, button#run:hover {
			color: #99ff99;
		}
		button#stop {
			text-decoration: none;
			color: #990000;
		}
		button#stop:hover {
			color: #ff0000;
		}
		#canvas {
			width: min(100%, 512px);
		}
	}
}
